div {
  font-family: "Apple SD Gothic Neo";
}

.bg_image {
  height: 100vh;
  max-width: 760px;
  margin: 0 auto;
  background-image: linear-gradient(
    rgba(225, 225, 225, 0),
    rgba(225, 225, 225, 0),
    rgba(225, 225, 225, 0),
    rgba(225, 225, 225, 0.3),
    rgba(225, 225, 225, 0.8),
    rgba(225, 225, 225, 0.9),
    rgba(225, 225, 225, 1)
  );
}

.header {
  height: 56px;
  display: flex;
  align-items: flex-end;
  justify-content: center;
}

img {
  height: 15px;
}

.contents {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  height: 73%;
  margin-top: 20px;
}

.bg_tab {
  display: flex;
  height: 30px;
  justify-content: center;
  align-items: center;
  gap: 8px;
}

.picked {
  background-color: #001935 !important;
}

.bg_tab div {
  width: 60px;
  height: 5px;
  background-color: #00193533;
  border-radius: 5px;
}

.bg_text p {
  margin-bottom: 20px;
  color: #000d1b99;
  font-size: 1.05rem;
  font-weight: 500;
  letter-spacing: -0.8px;
  text-shadow: 5px 5px 5px rgba(255, 255, 255, 0.2);
}

