  background-position: center;
  background-size: cover;
  background-image: linear-gradient(
      rgba(225, 225, 225, 1),
      rgba(225, 225, 225, 1),
      rgba(225, 225, 225, 0.8),
      rgba(225, 225, 225, 0.6),
