.bottom {
  display: flex;
  align-items: center;
  justify-content: center;
}

.bottom a {
  display: flex;
  align-items: center;
  justify-content: center;
  color: #ffffff;
  position: fixed;
  width: 240px;
  height: 48px;
  bottom: 0;
  background-color: #001935;
  border-radius: 24px;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  margin-bottom: 48px;
  text-decoration: none;
  outline: none;
}

.bottom Â a:hover,
a:active {
